import * as React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import UserCreateForms from '../components/UserCreateForm';
import HobbiesAddingForm from '../components/HobbiesAddingForm';
import RefreshContextWarper from '../context/RefreshContext';
export default function FormsPage() {

    return (
        <div style={{ background: 'rgb(231, 235, 240)', minHeight: '100vh', paddingTop: 30 }}>
            <RefreshContextWarper>
                <Card sx={{ maxWidth: 500, margin: 'auto', }}>
                    <CardContent>
                        <UserCreateForms />
                    </CardContent>
                </Card>
                <Card sx={{ maxWidth: 500, margin: 'auto', marginTop: 10 }}>
                    <CardContent>
                        <HobbiesAddingForm />
                    </CardContent>
                </Card>
            </RefreshContextWarper>
        </div>
    )

}